<template>
     <div class="card mb-3">
         <div class="card-body">
            <form>
                <div class="form-group">
                    <div class="controls">
                        <textarea @focus="showForm = true" v-model="pollData.question" class="form-control is-rounded" placeholder=" What's your question?"></textarea>
                    </div>
                 </div>
                  <div class="mb-3">
                    <p v-for="(option, index) in pollData.options" :key="index">
                      <span class="fa fa-check"></span>
                      {{ option }}
                      <span @click="removeOption(option)" style="cursor: pointer;">Remove</span>
                    </p>  
                  </div>

                  <div v-if="showForm">
                      <div class="row mb-3">
                        <div class="col-md-12">
                          <div class="input-group">
                            <input v-model="option" type="text" class="form-control form-control-sm" placeholder="Add Option">
                            <div class="input-group-append">
                              <button type="button" @click="addOption" class="btn waves-effect waves-light btn-primary btn-sm">
                                Add
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="form-group">
                        <div class="text-xs-right">
                          <button type="button" class="btn btn-info btn-sm mr-3 py-2 px-3">Post</button>
                          <button type="reset" class="btn btn-link btn-sm" @click="showForm = !showForm" >Cancel</button>
                        </div>
                      </div>
                  </div>
            </form>
         </div>
     </div>
</template>

<script>
  export default {
    name:'pollform',
    data() {
      return {
        option: '',
        pollData: {
          question: '',
          options: []
        },
        showForm: false
      }
    },
    methods: {
      addOption () {
        this.pollData.options.push(this.option);
        this.option = '';
      }
    }
  }
</script>

